<template>
  <!-- 首页附近 -->
  <div class="nearby">
    <div class="nearby__title">附近店铺</div>

    <router-link
      :to="`/shop/${item.id}`"
      v-for="item in nearbyItem"
      :key="item.id"
    >
      <shopInfo :item="item" :showBorder="true" />
    </router-link>
  </div>
  <Toast v-show="isShow" :message="toastMessage" />
</template>

<script>
import { ref } from "vue";
import { get } from "../../utils/request";
import Toast, { showToastEffect } from "@/components/Toast.vue";
import ShopInfo from "@/components/ShopInfo.vue";

const useNearbyEffect = (showToast) => {
  const nearbyItem = ref([]);

  const getNearbyItem = async () => {
    try {
      const result = await get("/home/nearby");
      if (result.data.code === "0000") {
        nearbyItem.value = result.data.data;
      } else {
        showToast("请求数据为空");
      }
    } catch (error) {
      showToast("请求数据失败");
    }
  };
  getNearbyItem();

  return { nearbyItem };
};

export default {
  name: "HomeNearby",
  components: { Toast, ShopInfo },

  setup() {
    const { isShow, toastMessage, showToast } = showToastEffect();
    const { nearbyItem } = useNearbyEffect(showToast);

    return { nearbyItem, isShow, toastMessage };
  },
};
</script>

<style lang="scss" scoped>
.nearby {
  padding-bottom: 55rem;
  box-sizing: border-box;

  &__title {
    font-family: PingFangSC-Medium;
