<template>
  <!-- 导航栏 -->
  <section class="docker">
    <div
      :class="{ docker__item: true, docker__item__active: item.tag === tag }"
      @click="
        () => {
          dockerItemClick(item.path);
        }
      "
      v-for="item in dockerItem"
      :key="item.icon"
    >
      <span class="docker__item__iconfont iconfont" v-html="item.icon"></span>
      <span class="docker__item__text">{{ item.text }}</span>
    </div>
  </section>
</template>

<script>
import { useRouter } from "vue-router";
export default {
  name: "Docker",
  props: ["tag"],

  setup() {
    const router = useRouter();

    const dockerItem = [
      { icon: "&#xe638;", tag: "home", text: "首页", path: "/" },
      { icon: "&#xe6e8;", tag: "cart", text: "购物车", path: "/" },
      {
        icon: "&#xe787;",
        tag: "order",
        text: "订单",
        path: "/order/orderList",
      },
      { icon: "&#xe60d;", tag: "user", text: "我的", path: "/" },
    ];

    const dockerItemClick = (path) => {
      router.push({ path: path });
    };

    return { dockerItem, dockerItemClick };
  },
};
</script>

